import React from "react";
import styled from "styled-components";
import { Link } from "gatsby";
import scrollTo from "gatsby-plugin-smoothscroll";
import { Navbar, Nav } from "react-bootstrap";
import { useTheme } from "../styles";

import "bootstrap/dist/css/bootstrap.min.css";

const Logo = styled.h2`
	font-family: "Samarkan", sans-serif;
	color: ${props => props.theme.white};
`;

const LinkStyle = {
	fontFamily: "Open Sans, sans-serif",
	fontWeight: 500,
	fontSize: "1.1rem",
	color: "#e5e5e5",
	textDecorations: "none",
	marginLeft: "1rem",
};

const Header: React.FC = () => {
	const { darkBlue, yellow } = useTheme().colors;

	return (
		<nav>
			<Navbar collapseOnSelect expand="lg" variant="dark" style={{ backgroundColor: `${darkBlue}` }}>
				<Navbar.Brand href="/">
					<Logo>
						The <span style={{ color: `${yellow}` }}>yellow</span> filter
					</Logo>
				</Navbar.Brand>
				<Navbar.Toggle aria-controls="responsive-navbar-nav" />
				<Navbar.Collapse id="responsive-navbar-nav">
					<Nav className="ml-auto">
						<Nav.Item>
							<Link style={LinkStyle} to="/">
								Home
							</Link>
						</Nav.Item>

						<Nav.Item>
							<Link style={LinkStyle} to="/interior-decorations">
								Interior Decorations
							</Link>
						</Nav.Item>

						<Nav.Item>
							<Link style={LinkStyle} to="/fashion-photography">
								Fashion and Photography
							</Link>
						</Nav.Item>

						<Nav.Item style={LinkStyle} onClick={() => scrollTo("#contact")}>
							Contact Us
						</Nav.Item>
					</Nav>
				</Navbar.Collapse>
			</Navbar>
		</nav>
	);
};

export default Header;
