import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { Card } from "react-bootstrap";

import { firebaseapp } from "../utils";
import "firebase/database";

const Container = styled.div`
	padding: ${props => (props.theme.screens.md ? "7vw" : "3vw 7vw 3vw 7vw")};
`;

const CardContainer = styled.div`
	display: flex;
	flex-direction: ${props => (props.theme.screens.md ? "column" : "row")};
	flex-wrap: wrap;
`;

const Head = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	display: inline-block;
	padding-bottom: 1.5rem;
	border-bottom: 2px solid black;
`;

const CardWrapper = styled.div`
	margin: ${props => (props.theme.screens.md ? "1rem 0rem 1rem 0rem" : "0rem 2rem 0rem 0rem")};
	font-family: "Open Sans", sans-serif;
`;

const ContactUs: React.FC = () => {
	const [contactNumber, setContactNumber] = useState([]);

	useEffect(() => {
		firebaseapp
			.database()
			.ref("contact_number")
			.once("value")
			.then(snapshot => setContactNumber(snapshot.val()));
	}, []);

	return (
		<Container>
			<Head>Contact Us.</Head>
			<br></br>
			<br></br>

			<CardContainer>
				<CardWrapper>
					<Card>
						<Card.Header>
							<i className="fab fa-whatsapp"></i> Call / Whatsapp
						</Card.Header>
						<Card.Body>
							<blockquote className="blockquote mb-0">
								<h4>{contactNumber[0]}</h4>
								<h4>{contactNumber[1]}</h4>
								<h4>{contactNumber[2]}</h4>
							</blockquote>
						</Card.Body>
					</Card>
				</CardWrapper>

				<CardWrapper>
					<Card>
						<Card.Header>
							<i className="fas fa-envelope-open-text"></i> Email Us
						</Card.Header>
						<Card.Body>
							<blockquote className="blockquote mb-0">
								<a href="mailto:theyellowfilterme@gmail.com">theyellowfilterme@gmail.com</a>
							</blockquote>
						</Card.Body>
					</Card>
				</CardWrapper>
			</CardContainer>
		</Container>
	);
};

export default ContactUs;
